
main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}

:root {
  --primary: #272727;
  --secondary: #14A76C;
  --tertiary: #FF652F;
  --text: #FEFFFF;
  --light: #555555;
  --pop: #FFE400;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: var(--primary);
  color: var(--text);
  font-family: 'Staatliches', cursive;

}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Staatliches', cursive;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.75rem;
}

h2 {
  font-size: 2.5rem;
  color: var(--text);
}

h3 {
  font-size: 2.25rem;
}

h4 {
  font-size: 2.0rem;
}

h5 {
  font-size: 1.75rem;
}

h6 {
  font-size: 1.5rem;
}

p {
  font-size: 1.25rem;
  margin-top: 0;
}

a:hover {
  text-decoration: none;
  color: var(--secondary);
}

header a {
  color: var(--text);
}

