button:hover {
  opacity: .8;
  
}

button[disabled] {
  cursor: default;
  opacity: .8;
  
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav  li {
  cursor: pointer;
}

label {
  padding-right: .5rem;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

textarea:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

a {
  border-radius: 5px;
  font-family: 'Staatliches', cursive;
	color: var(--text);
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
	
}

.title {
  color: var(--primary);
}

.footer {
  display: flex;
  justify-content: center;
}

footer a {
  color: var(--text);
  opacity: 1;
  margin: 10px 20px;
  transition: all .3s ease-in-out;
}

footer a:hover {
  color: var(--secondary);
}

.portrait {
  width: 42%; 
  height: 42%;
}


.project {
  position: relative;
  width: 100%;
  max-width: 500px;
  height: 300px;
  margin: 30px;
}

.project-bg {
  position: relative;
  width: 100%;
  height: 100%;
}

.project-text {
    color: var(--tertiary);
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding: 50px;
    z-index: 900;
    opacity: 0;
    transition: all .5s ease-in-out;
}

.project-text:hover {
  background-color: rgb(39, 39, 39, 0.85);
  opacity: 1;
  border: 5px solid var(--tertiary);
}

.project-text h3 {
  font-size: 2.5rem;
}

.project-text a {
  text-decoration: underline;
  font-weight: bold;
  color: var(--tertiary);
  transition: all .3s ease-in-out;
}

.project-text a:hover {
  color: var(--tertiary);
  text-decoration: none;
}

.homeh2 {
  font-size: 2rem;
  border-top: 2px solid var(--pop);
  border-bottom: 2px solid var(--secondary);
  padding: 6rem;
}

.resume {
  text-decoration: underline;
}

.resume:hover {
  text-decoration: none;
}

.nav-link.active {
 border: 2px solid var(--secondary);
}

.who,
.what,
.contact {
  border-top: 2px solid var(--pop);
  border-bottom: 2px solid var(--secondary);
}
